<template>
  <div class="shopping-list">
    <div class="write-icon-wrapper" v-show="quick_toggled == false && write_toggled == false">
      <div class="write-icon" v-show="write_toggled == false && quick_toggled == false" @click="toggler('write')">
        <img src="./../assets/write.png" />
      </div>
      <div class="quick-icon" v-show="quick_toggled == false && write_toggled == false" @click="toggler('quick')">
        <img src="./../assets/quick.png" />
      </div>
    </div>
    <div id="addItem-wrapper" class="w-full" v-show="write_toggled == true">
      <div class="write-icon-wrapper">
        <div class="back-icon" @click="toggleReset()">
          <img src="./../assets/back.png" />
        </div>
      </div>
      <form @submit="addItem(name, amount)" class="bg-white">
        <div class="flex-nowrap">
          <input id="item-name" v-model="name" placeholder="Vul hier in" class="shadow appearance-none border rounded px-2 py-1 focus:outline-none focus:shadow-outline">
          <div id="item-minus" @click="addItemChangeAmount('substract')" class="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-3 rounded">-</div>
          <input id="item-amount" v-model="amount" placeholder="amount" type="text" class="px-2 py-1" readonly>
          <div id="item-plus" @click="addItemChangeAmount('add')" class="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-3 rounded">&#43;</div>
        </div>
        <button id="item-submit" type="submit" class="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-3 rounded">Toevoegen</button>
      </form>
    </div>
    <div id="products-wrapper" v-show="quick_toggled == true">
      <div class="write-icon-wrapper">
        <div class="back-icon" @click="toggler('quick')">
          <img src="../assets/back.png" />
        </div>
      </div>
      <div id="product-list">
        <div class="product-data" :class="{'active': (product.enabled == 1)}" @click="addItemQuick(product.name, '1', product)" v-for="(product, idx) in productlist" :key="idx">
          <div class="product-image"><img :src=product.imgUrl /></div>
          <div class="product-description" >{{product.name}}</div>
        </div>
      </div>
    </div>
    <div id="item-list" v-show="quick_toggled == false">
      <div class="item-data" v-for="(item, idx) in items" :key="idx">
        <div @click="increaseExistingAmount(item)" class="item-list-modifier bg-green-500 hover:bg-green-700 text-white font-bold rounded">+</div>
        <div @click="decreaseExistingAmount(item)" class="item-list-modifier bg-blue-500 hover:bg-blue-700 text-white font-bold rounded">-</div>
        <div class="item-description">{{item.amount}} x {{item.name}}</div>
        <button @click="deleteItem(item.id, item.name)" class="item-list-modifier item-delete bg-red-500 hover:bg-red-700 text-white font-bold rounded">
          X
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from '../main'
export default {
  name: 'List',
  data () {
    return {
      items: [],
      productlist: [],
      name: '',
      amount: '1',
      write_toggled: false,
      quick_toggled: false
    }
  },
  firestore () {
    return {
      items: db.collection('list'),
      productlist: db.collection('product')
    }
  },
  methods: {
    addItem (name, amount) {
      console.log('firing addItem')
      if (this.name !== '') {
        var alreadyInList = false
        // check for duplicates
        for (var i = 0; i < this.items.length; i++) {
          if (this.items[i].name.localeCompare(name, undefined, { sensitivity: 'base' }) === 0) {
            console.log('This item is in the list! SKIP!')
            alreadyInList = true
          }
        }
        if (alreadyInList === false) {
          db.collection('list').add({name, amount})
          // check if this is already among the products
          console.log('You wrote: ' + name)
          this.checkProductList(name, 'add')

          this.toggler('write')
        }
        // reset form field values
        this.name = ''
        this.amount = '1'
      }
    },
    addItemChangeAmount (amount) {
      if (amount === 'add') {
        this.amount++
      } else if (amount === 'substract' && this.amount > 1) {
        this.amount--
      }
    },
    addItemQuick (name, amount, object) {
      console.log('quick! You clicked ' + name)
      var quickAdd = true
      if (this.items.length === 0) {
        console.log('List was empty, adding: ' + name)
        quickAdd = false
        db.collection('list').add({name, amount})
        // enable product on product list, used to add active class
        this.productSwitch(object, '1')
      } else {
        for (var i = 0; i < this.items.length; i++) {
          if (this.items[i].name.localeCompare(name, undefined, { sensitivity: 'base' }) === 0) {
            console.log('I found: ' + this.items[i].name)
            quickAdd = false
          }
        }
      }
      if (quickAdd === true) {
        console.log('not on the list, adding!')
        db.collection('list').add({name, amount})
        // enable product on product list, used to add active class
        this.productSwitch(object, '1')
      }
    },
    toggler (e) {
      if (e === 'write') {
        this.write_toggled = !this.write_toggled
      } else if (e === 'quick') {
        this.quick_toggled = !this.quick_toggled
      }
    },
    toggleReset () {
      this.write_toggled = false
      this.quick_toggled = false
    },
    checkProductList (itemName, actionList) {
      if (this.productlist.length !== 0) {
        var foundName = false
        var foundObject = []
        console.log('You are now in checkProductList. itemName is now: ' + itemName)
        for (var i = 0; i < this.productlist.length; i++) {
          if (this.productlist[i].name.localeCompare(itemName, undefined, { sensitivity: 'base' }) === 0) {
            console.log('checkProductList loop name is: ' + this.productlist[i].name)
            foundName = true
            foundObject = this.productlist[i]
            console.log('I found: ' + foundObject.name)
          }
        }
        if (foundName === true) {
          // update product to enable itself
          console.log('foundName is true. productlist[i] naam is: ' + foundObject.name)
          if (actionList === 'add') {
            this.productSwitch(foundObject, '1')
          } else if (actionList === 'remove') {
            this.productSwitch(foundObject, '0')
          }
        }
      }
    },
    productSwitch (object, toggle) {
      db.collection('product').doc(object.id).update({enabled: toggle})
    },
    increaseExistingAmount (object) {
      db.collection('list').doc(object.id).update({amount: Number(object.amount) + 1})
    },
    decreaseExistingAmount (object) {
      if (Number(object.amount) > 1) {
        db.collection('list').doc(object.id).update({amount: Number(object.amount) - 1})
      }
    },
    deleteItem (id, name) {
      this.checkProductList(name, 'remove')
      db.collection('list').doc(id).delete()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.write-icon-wrapper {
  padding:10px;
  display:flex;
  flex-wrap:nowrap;
  justify-content:space-between;
}
.write-icon,
.quick-icon {
  width:48%;
}
.back-icon,
.quick-icon,
.write-icon {
  padding:10px;
  background-color:#48bb78;
  border-radius:10px;
  text-align:center;
}
.back-icon:hover,
.quick-icon:hover,
.write-icon:hover {
  cursor:pointer;
}
.back-icon {
  width:100%;
  background-color:#4299e1;
}
.write-icon img {
  display:inline-block;
  width:30px;
}
.back-icon img,
.quick-icon img {
  display:inline-block;
  width:48px;
}
#addItem-wrapper {
  padding:10px;
}
#addItem-wrapper form .flex-nowrap {
  display:flex;
  flex-wrap:nowrap;
}
#addItem-wrapper .write-icon-wrapper {
  padding:0 0 14px;
}
#item-name {
  margin-right:20px;
  min-width:0;
}
#item-amount {
  width:40px;
  text-align:center;
}
#item-minus:hover,
#item-plus:hover {
  cursor:pointer;
}
#item-submit {
  margin:20px 0 0;
  width:100%;
}
#item-list {

}
.item-data {
  display:flex;
  justify-content:flex-start;
  padding:10px;
}
.item-data:nth-child(even){
  background-color:#efefef;
}
.item-data.active {
  color:green;
}
.item-description {
font-size:16px;
line-height:2.5;
margin:0 0 0 5px;
text-transform:capitalize;
}
.item-delete {
  margin-left:auto!important;
}
.item-list-modifier {
  line-height:2.5;
  width:30px;
  margin:0 5px 0 0;
}
#product-list .product-data:hover,
.item-list-modifier:hover {
  cursor:pointer;
}
#product-list {
  display:flex;
  justify-content:space-between;
  padding:10px;
  flex-wrap:wrap;
}
#product-list .product-data {
  width:30%;
  background-color:#efefef;
  padding:5px;
  border-radius:10px;
  border:1px solid #efefef;
  margin:0 0 20px;
}
#product-list .product-data.active {
  border-color:#006400;
  background-color:#90EE90;
}
#product-list .product-description {
  text-align:center;
}
a {
  color: #42b983;
}
</style>



