<template>
  <div class="shopping-list">
    <div id="addItem-wrapper" class="w-full">
      <h1>Add product</h1>
      <form @submit="addItem(name, imgUrl, 0)" class="bg-white">
        <div class="flex-nowrap">
          <input id="item-name" v-model="name" placeholder="name" class="shadow appearance-none border rounded px-2 py-1 focus:outline-none focus:shadow-outline">
          <input id="item-name" v-model="imgUrl" placeholder="imgUrl(optional)" class="shadow appearance-none border rounded px-2 py-1 focus:outline-none focus:shadow-outline">
          <input id="item-name" v-model="enabled" value="0" class="shadow appearance-none border rounded px-2 py-1 focus:outline-none focus:shadow-outline">
        </div>
        <button id="item-submit" type="submit" class="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-3 rounded">Toevoegen</button>
      </form>
    </div>
    <div id="item-list">
      <div class="item-data" v-for="(item, idx) in products" :key="idx">
        <div class="item-image"><img :src="getImgUrl(item.name, item.imgUrl)" height="30"/></div>
        <div class="item-description">{{item.name}}</div>
        <button @click="deleteItem(item.id)" class="item-list-modifier item-delete bg-red-500 hover:bg-red-700 text-white font-bold rounded">
          X
        </button>
      </div>
    </div>

  </div>
</template>

<script>
import { db } from '../main'
export default {
  name: 'Products',
  data () {
    return {
      products: [],
      name: '',
      imgUrl: '',
      enabled: '0'
    }
  },
  firestore () {
    return {
      products: db.collection('product')
    }
  },
  methods: {
    addItem (name, imgUrl, enabled) {
      if (this.name !== '' && this.imgUrl !== '') {
        db.collection('product').add({name, imgUrl, enabled})
        // reset form field values
        this.name = ''
        this.imgUrl = ''
      }
    },
    deleteItem (id) {
      db.collection('product').doc(id).delete()
    },
    getImgUrl (localImg, extImg) {
      if (extImg === '') {
        // no more local loading
        // return require('../assets/products/' + localImg.toLowerCase() + '.jpg')
      } else if (extImg !== '') {
        return extImg
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#addItem-wrapper form .flex-nowrap {
  display:flex;
  flex-wrap:nowrap;
  padding:0 10px;
  justify-content:center;
}
#item-name {
  margin:0 0 20px;
  min-width:0;
}
#item-amount {
  width:40px;
  text-align:center;
}
#item-minus:hover,
#item-plus:hover {
  cursor:pointer;
}
#item-submit {

}
#item-list {

}
.item-data {
  display:flex;
  justify-content:flex-start;
  padding:10px;
}
.item-data:nth-child(even){
  background-color:#efefef;
}
.item-image img {
  display:block;
  height:50px;
  margin:0 10px 0 0;
}
.item-description {
font-size:16px;
line-height:2.5;
margin:0 0 0 5px;
text-transform:capitalize;
}
.item-delete {
  margin-left:auto!important;
}
.item-list-modifier {
  line-height:2.5;
  width:30px;
  margin:0 5px 0 0;
}
.item-list-modifier:hover {
  cursor:pointer;
}
a {
  color: #42b983;
}
</style>



